import { memo } from 'react';
import { Download, Maximize2 } from 'lucide-react';

/**
 *    ( )
 */
const ChartContainer = memo(({
    title,
    subtitle,
    children,
    actions,
    className = '',
    height = 350,
    loading = false,
}) => {
    if (loading) {
        return (
            <div className={`card ${className}`}>
                <div className="card-header">
                    <div>
                        <div className="skeleton" style={{ width: 150, height: 20, marginBottom: 8 }} />
                        <div className="skeleton" style={{ width: 200, height: 14 }} />
                    </div>
                </div>
                <div className="skeleton" style={{ width: '100%', height }} />
            </div>
        );
    }

    return (
        <div className={`card ${className} animate-fade-in`}>
            <div className="card-header">
                <div>
                    <h3 className="card-title">{title}</h3>
                    {subtitle && <p className="card-subtitle">{subtitle}</p>}
                </div>

                <div style={{ display: 'flex', gap: 8 }}>
                    {actions}
                    <button className="btn btn-ghost btn-icon" title="">
                        <Maximize2 size={16} />
                    </button>
                    <button className="btn btn-ghost btn-icon" title="">
                        <Download size={16} />
                    </button>
                </div>
            </div>

            <div className="chart-container" style={{ height }}>
                {children}
            </div>
        </div>
    );
});

ChartContainer.displayName = 'ChartContainer';

export default ChartContainer;

