import { useState, useMemo } from 'react';
import {
    Train, MapPin, TrendingUp, Calendar, Building2,
    Clock, CheckCircle, AlertCircle, ChevronDown, ChevronUp,
    Star, Filter, Search
} from 'lucide-react';
import { formatNumber, formatPercent } from '../utils/formatters';

/**
 *   
 * GTX,  ,      
 */
const TransportAnalysis = () => {
    const [selectedCategory, setSelectedCategory] = useState('all');
    const [selectedStatus, setSelectedStatus] = useState('all');
    const [expandedProject, setExpandedProject] = useState(null);
    const [searchQuery, setSearchQuery] = useState('');

    //     (2025 12 )
    const transportProjects = useMemo(() => [
        // GTX
        {
            id: 1,
            name: 'GTX-A',
            category: 'gtx',
            status: 'operating',
            completionDate: '2024.03',
            route: '       ',
            stations: ['', '', '', '', '', '', '', '', '', ''],
            benefitAreas: [
                { name: '', region: '', priceChange: 15.2, avgPrice: 52000 },
                { name: '', region: '', priceChange: 12.8, avgPrice: 48000 },
                { name: '2', region: '', priceChange: 18.5, avgPrice: 62000 },
            ],
            description: ' . ~  83.1km  1 .',
            travelTime: '- 20',
            importance: 'high',
        },
        {
            id: 2,
            name: 'GTX-B',
            category: 'gtx',
            status: 'construction',
            completionDate: '2030.12',
            route: '        ',
            stations: ['', '', '', '', '', '', '', '', '', ''],
            benefitAreas: [
                { name: '', region: '', priceChange: 8.5, avgPrice: 58000 },
                { name: '', region: '', priceChange: 11.2, avgPrice: 45000 },
                { name: '', region: '', priceChange: 6.8, avgPrice: 28000 },
            ],
            description: ' ~   80.1km . 2024 .',
            travelTime: '- 25 ',
            importance: 'high',
        },
        {
            id: 3,
            name: 'GTX-C',
            category: 'gtx',
            status: 'construction',
            completionDate: '2028.12',
            route: '         ',
            stations: ['', '', '', '', '', '', '', '', ''],
            benefitAreas: [
                { name: '', region: '', priceChange: 22.5, avgPrice: 32000 },
                { name: '', region: '', priceChange: 14.3, avgPrice: 42000 },
                { name: '', region: '', priceChange: 9.8, avgPrice: 55000 },
            ],
            description: '   . ~  74.8km.',
            travelTime: '- 20 ',
            importance: 'high',
        },

        //  
        {
            id: 4,
            name: '',
            category: 'subway',
            status: 'construction',
            completionDate: '2025.12',
            route: '      ',
            stations: ['', '', '', '', '', ''],
            benefitAreas: [
                { name: '', region: '', priceChange: 25.3, avgPrice: 38000 },
                { name: '', region: '', priceChange: 18.7, avgPrice: 52000 },
                { name: '', region: '', priceChange: 12.4, avgPrice: 35000 },
            ],
            description: '~ 44.7km.   .',
            travelTime: '- 25 ',
            importance: 'high',
        },
        {
            id: 5,
            name: '9 4 ',
            category: 'subway',
            status: 'construction',
            completionDate: '2027.06',
            route: '    ',
            stations: ['', '', ''],
            benefitAreas: [
                { name: '', region: '', priceChange: 8.2, avgPrice: 68000 },
                { name: '', region: '', priceChange: 5.5, avgPrice: 72000 },
            ],
            description: '9  .   .',
            travelTime: '- 35 ',
            importance: 'medium',
        },
        {
            id: 6,
            name: '7  ',
            category: 'subway',
            status: 'construction',
            completionDate: '2027.12',
            route: '    ',
            stations: ['', '', '', ''],
            benefitAreas: [
                { name: '', region: '', priceChange: 15.8, avgPrice: 48000 },
            ],
            description: '7   .  .',
            travelTime: '- 50 ',
            importance: 'medium',
        },
        {
            id: 7,
            name: '',
            category: 'subway',
            status: 'construction',
            completionDate: '2028.06',
            route: '      ',
            stations: ['', '', '', '', '', ''],
            benefitAreas: [
                { name: '', region: '', priceChange: 12.5, avgPrice: 85000 },
            ],
            description: '~ 14.8km .',
            travelTime: '- 25 ',
            importance: 'medium',
        },

        // 
        {
            id: 8,
            name: ' ',
            category: 'rail',
            status: 'planning',
            completionDate: '2032.12',
            route: '      ',
            stations: ['', '', '', ''],
            benefitAreas: [
                { name: '', region: '', priceChange: 5.2, avgPrice: 45000 },
            ],
            description: '   .',
            travelTime: '- 20 ',
            importance: 'low',
        },
        {
            id: 9,
            name: '  ',
            category: 'subway',
            status: 'planning',
            completionDate: '2031.12',
            route: '    ',
            stations: ['', '', '', ''],
            benefitAreas: [
                { name: '', region: '', priceChange: 3.5, avgPrice: 95000 },
            ],
            description: '   .',
            travelTime: '- 15 ',
            importance: 'medium',
        },
        {
            id: 10,
            name: '',
            category: 'subway',
            status: 'construction',
            completionDate: '2029.06',
            route: '      ',
            stations: ['', '', '', '', ''],
            benefitAreas: [
                { name: '', region: '', priceChange: 8.9, avgPrice: 52000 },
                { name: '', region: '', priceChange: 6.2, avgPrice: 48000 },
            ],
            description: '  . 13.4km.',
            travelTime: '- 20 ',
            importance: 'medium',
        },
    ], []);

    const categories = [
        { id: 'all', label: '', icon: Train },
        { id: 'gtx', label: 'GTX', icon: Train },
        { id: 'subway', label: '', icon: MapPin },
        { id: 'rail', label: '', icon: Train },
    ];

    const statuses = [
        { id: 'all', label: '' },
        { id: 'operating', label: ' ' },
        { id: 'construction', label: ' ' },
        { id: 'planning', label: '' },
    ];

    const getStatusBadge = (status) => {
        switch (status) {
            case 'operating':
                return { label: ' ', className: 'badge-success' };
            case 'construction':
                return { label: ' ', className: 'badge-warning' };
            case 'planning':
                return { label: '', className: 'badge-secondary' };
            default:
                return { label: status, className: 'badge-secondary' };
        }
    };

    const getImportanceStars = (importance) => {
        const count = importance === 'high' ? 3 : importance === 'medium' ? 2 : 1;
        return Array(count).fill(null).map((_, i) => (
            <Star key={i} size={12} fill="var(--color-warning)" stroke="var(--color-warning)" />
        ));
    };

    const filteredProjects = useMemo(() => {
        return transportProjects.filter(project => {
            if (selectedCategory !== 'all' && project.category !== selectedCategory) return false;
            if (selectedStatus !== 'all' && project.status !== selectedStatus) return false;
            if (searchQuery && !project.name.toLowerCase().includes(searchQuery.toLowerCase()) &&
                !project.route.includes(searchQuery)) return false;
            return true;
        });
    }, [transportProjects, selectedCategory, selectedStatus, searchQuery]);

    // 
    const stats = useMemo(() => {
        const operating = transportProjects.filter(p => p.status === 'operating').length;
        const construction = transportProjects.filter(p => p.status === 'construction').length;
        const planning = transportProjects.filter(p => p.status === 'planning').length;
        const totalBenefitAreas = transportProjects.reduce((sum, p) => sum + p.benefitAreas.length, 0);

        return { operating, construction, planning, totalBenefitAreas };
    }, [transportProjects]);

    return (
        <div className="page-container">
            {/* Header */}
            <div className="card" style={{ marginBottom: 24 }}>
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', flexWrap: 'wrap', gap: 16 }}>
                    <div style={{ display: 'flex', alignItems: 'center', gap: 12 }}>
                        <div style={{
                            width: 40,
                            height: 40,
                            borderRadius: 'var(--radius-md)',
                            background: 'linear-gradient(135deg, #6366f1, #8b5cf6)',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            color: 'white',
                        }}>
                            <Train size={20} />
                        </div>
                        <div>
                            <h2 style={{ fontSize: '1.125rem', fontWeight: 600 }}> </h2>
                            <p style={{ fontSize: '0.8rem', color: 'var(--color-text-tertiary)' }}>
                                GTX,  ,   
                            </p>
                        </div>
                    </div>

                    <div style={{ position: 'relative' }}>
                        <Search size={16} style={{ position: 'absolute', left: 12, top: '50%', transform: 'translateY(-50%)', color: 'var(--color-text-tertiary)' }} />
                        <input
                            type="text"
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            placeholder=",  ..."
                            className="input"
                            style={{ paddingLeft: 36, width: 200 }}
                        />
                    </div>
                </div>
            </div>

            {/* Stats */}
            <div style={{ display: 'grid', gridTemplateColumns: 'repeat(4, 1fr)', gap: 16, marginBottom: 24 }}>
                <div className="stat-card">
                    <div className="stat-card-header">
                        <span> </span>
                        <CheckCircle size={18} style={{ color: 'var(--color-success)' }} />
                    </div>
                    <div className="stat-card-value">{stats.operating}</div>
                    <div className="stat-card-label"> </div>
                </div>
                <div className="stat-card">
                    <div className="stat-card-header">
                        <span> </span>
                        <Clock size={18} style={{ color: 'var(--color-warning)' }} />
                    </div>
                    <div className="stat-card-value">{stats.construction}</div>
                    <div className="stat-card-label"> </div>
                </div>
                <div className="stat-card">
                    <div className="stat-card-header">
                        <span></span>
                        <Calendar size={18} style={{ color: 'var(--color-text-tertiary)' }} />
                    </div>
                    <div className="stat-card-value">{stats.planning}</div>
                    <div className="stat-card-label"> </div>
                </div>
                <div className="stat-card">
                    <div className="stat-card-header">
                        <span> </span>
                        <Building2 size={18} style={{ color: 'var(--color-primary)' }} />
                    </div>
                    <div className="stat-card-value">{stats.totalBenefitAreas}</div>
                    <div className="stat-card-label"> </div>
                </div>
            </div>

            {/* Filters */}
            <div style={{ display: 'flex', gap: 16, marginBottom: 24, flexWrap: 'wrap' }}>
                <div style={{ display: 'flex', gap: 8 }}>
                    {categories.map(cat => (
                        <button
                            key={cat.id}
                            onClick={() => setSelectedCategory(cat.id)}
                            className={`btn ${selectedCategory === cat.id ? 'btn-primary' : 'btn-ghost'}`}
                            style={{ display: 'flex', alignItems: 'center', gap: 6 }}
                        >
                            <cat.icon size={16} />
                            {cat.label}
                        </button>
                    ))}
                </div>

                <div style={{ display: 'flex', gap: 8 }}>
                    {statuses.map(status => (
                        <button
                            key={status.id}
                            onClick={() => setSelectedStatus(status.id)}
                            className={`btn ${selectedStatus === status.id ? 'btn-secondary' : 'btn-ghost'}`}
                            style={{ fontSize: '0.85rem' }}
                        >
                            {status.label}
                        </button>
                    ))}
                </div>
            </div>

            {/* Projects List */}
            <div style={{ display: 'flex', flexDirection: 'column', gap: 16 }}>
                {filteredProjects.map(project => {
                    const isExpanded = expandedProject === project.id;
                    const statusBadge = getStatusBadge(project.status);

                    return (
                        <div
                            key={project.id}
                            className="card"
                            style={{ padding: 0, overflow: 'hidden' }}
                        >
                            {/* Header */}
                            <div
                                style={{
                                    display: 'flex',
                                    alignItems: 'center',
                                    padding: '20px 24px',
                                    cursor: 'pointer',
                                    borderBottom: isExpanded ? '1px solid var(--color-border)' : 'none',
                                }}
                                onClick={() => setExpandedProject(isExpanded ? null : project.id)}
                            >
                                {/* Icon */}
                                <div style={{
                                    width: 48,
                                    height: 48,
                                    borderRadius: 'var(--radius-md)',
                                    background: project.category === 'gtx' ? 'linear-gradient(135deg, #ef4444, #f97316)' :
                                        project.category === 'subway' ? 'linear-gradient(135deg, #3b82f6, #06b6d4)' :
                                            'linear-gradient(135deg, #10b981, #84cc16)',
                                    display: 'flex',
                                    alignItems: 'center',
                                    justifyContent: 'center',
                                    color: 'white',
                                    marginRight: 16,
                                    flexShrink: 0,
                                }}>
                                    <Train size={24} />
                                </div>

                                {/* Info */}
                                <div style={{ flex: 1 }}>
                                    <div style={{ display: 'flex', alignItems: 'center', gap: 8, marginBottom: 4 }}>
                                        <h3 style={{ fontSize: '1.1rem', fontWeight: 600 }}>{project.name}</h3>
                                        <span className={`badge ${statusBadge.className}`}>{statusBadge.label}</span>
                                        <div style={{ display: 'flex', gap: 2 }}>{getImportanceStars(project.importance)}</div>
                                    </div>
                                    <p style={{ fontSize: '0.85rem', color: 'var(--color-text-secondary)' }}>
                                        {project.route}
                                    </p>
                                </div>

                                {/* Completion */}
                                <div style={{ textAlign: 'right', marginRight: 16 }}>
                                    <p style={{ fontSize: '0.75rem', color: 'var(--color-text-tertiary)' }}>
                                        {project.status === 'operating' ? '' : ''}
                                    </p>
                                    <p style={{ fontSize: '1rem', fontWeight: 600, color: 'var(--color-primary)' }}>
                                        {project.completionDate}
                                    </p>
                                </div>

                                <div style={{ color: 'var(--color-text-tertiary)' }}>
                                    {isExpanded ? <ChevronUp size={20} /> : <ChevronDown size={20} />}
                                </div>
                            </div>

                            {/* Expanded Content */}
                            {isExpanded && (
                                <div style={{ padding: 24, background: 'var(--color-bg-tertiary)' }}>
                                    {/* Description */}
                                    <p style={{ marginBottom: 20, color: 'var(--color-text-secondary)', lineHeight: 1.6 }}>
                                        {project.description}
                                    </p>

                                    {/* Travel Time */}
                                    <div style={{
                                        display: 'inline-flex',
                                        alignItems: 'center',
                                        gap: 8,
                                        padding: '8px 16px',
                                        background: 'var(--color-primary-light)',
                                        borderRadius: 'var(--radius-full)',
                                        marginBottom: 20,
                                    }}>
                                        <Clock size={16} style={{ color: 'var(--color-primary)' }} />
                                        <span style={{ fontSize: '0.9rem', fontWeight: 500, color: 'var(--color-primary)' }}>
                                            {project.travelTime}
                                        </span>
                                    </div>

                                    {/* Stations */}
                                    <div style={{ marginBottom: 20 }}>
                                        <h4 style={{ fontSize: '0.9rem', fontWeight: 600, marginBottom: 12 }}></h4>
                                        <div style={{ display: 'flex', flexWrap: 'wrap', gap: 8 }}>
                                            {project.stations.map((station, idx) => (
                                                <span
                                                    key={idx}
                                                    style={{
                                                        padding: '6px 12px',
                                                        background: 'var(--color-bg-secondary)',
                                                        borderRadius: 'var(--radius-sm)',
                                                        fontSize: '0.8rem',
                                                        border: '1px solid var(--color-border)',
                                                    }}
                                                >
                                                    {station}
                                                </span>
                                            ))}
                                        </div>
                                    </div>

                                    {/* Benefit Areas */}
                                    <div>
                                        <h4 style={{ fontSize: '0.9rem', fontWeight: 600, marginBottom: 12 }}>
                                              ({project.benefitAreas.length})
                                        </h4>
                                        <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fill, minmax(200px, 1fr))', gap: 12 }}>
                                            {project.benefitAreas.map((area, idx) => (
                                                <div
                                                    key={idx}
                                                    style={{
                                                        padding: 16,
                                                        background: 'var(--color-bg-secondary)',
                                                        borderRadius: 'var(--radius-md)',
                                                        border: '1px solid var(--color-border)',
                                                    }}
                                                >
                                                    <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', marginBottom: 8 }}>
                                                        <div>
                                                            <p style={{ fontWeight: 600, marginBottom: 2 }}>{area.name}</p>
                                                            <p style={{ fontSize: '0.75rem', color: 'var(--color-text-tertiary)' }}>{area.region}</p>
                                                        </div>
                                                        <div style={{
                                                            display: 'flex',
                                                            alignItems: 'center',
                                                            gap: 4,
                                                            color: area.priceChange >= 0 ? 'var(--color-success)' : 'var(--color-danger)',
                                                            fontSize: '0.9rem',
                                                            fontWeight: 600,
                                                        }}>
                                                            <TrendingUp size={14} />
                                                            {formatPercent(area.priceChange)}
                                                        </div>
                                                    </div>
                                                    <p style={{ fontSize: '0.8rem', color: 'var(--color-text-secondary)' }}>
                                                         {formatNumber(area.avgPrice)}/3.3
                                                    </p>
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                </div>
                            )}
                        </div>
                    );
                })}
            </div>

            {/* Empty State */}
            {filteredProjects.length === 0 && (
                <div className="empty-state">
                    <Train className="empty-state-icon" />
                    <h3 className="empty-state-title">  </h3>
                    <p className="empty-state-desc">    </p>
                </div>
            )}

            {/* Info */}
            <div style={{
                marginTop: 24,
                padding: 16,
                background: 'var(--color-bg-tertiary)',
                borderRadius: 'var(--radius-md)',
                fontSize: '0.8rem',
                color: 'var(--color-text-tertiary)',
            }}>
                        .      .
                <br />
                     ,     .
            </div>
        </div>
    );
};

export default TransportAnalysis;
