/**
 * Google News RSS    
 */
export const fetchRealEstateNews = async (category = 'all') => {
    const queries = {
        all: '',
        market: '+',
        policy: '+',
        analysis: '+',
        development: '+',
    };

    const query = queries[category] || queries.all;
    const rssUrl = `https://news.google.com/rss/search?q=${encodeURIComponent(query)}&hl=ko&gl=KR&ceid=KR:ko`;

    try {
        //   RSS 
        const response = await fetch(`/api/rss?url=${encodeURIComponent(rssUrl)}`);

        if (!response.ok) {
            throw new Error('RSS   ');
        }

        const xmlText = await response.text();
        return parseRSSFeed(xmlText);
    } catch (error) {
        console.error('  :', error);
        // :   
        return fetchNewsFromBackup(category);
    }
};

/**
 * RSS XML 
 */
function parseRSSFeed(xmlText) {
    const parser = new DOMParser();
    const xml = parser.parseFromString(xmlText, 'text/xml');
    const items = xml.querySelectorAll('item');

    const news = [];
    items.forEach((item, index) => {
        if (index >= 20) return; //  20

        const title = item.querySelector('title')?.textContent || '';
        const link = item.querySelector('link')?.textContent || '';
        const pubDate = item.querySelector('pubDate')?.textContent || '';
        const source = item.querySelector('source')?.textContent || '';
        const description = item.querySelector('description')?.textContent || '';

        // HTML  
        const cleanDescription = description.replace(/<[^>]*>/g, '').substring(0, 200);

        news.push({
            id: index + 1,
            title: title.replace(' - ' + source, ''),
            summary: cleanDescription,
            source,
            date: new Date(pubDate).toISOString().split('T')[0],
            link,
            category: categorizeNews(title),
            sentiment: analyzeSentiment(title),
        });
    });

    return news;
}

/**
 *    
 */
function categorizeNews(title) {
    const lowerTitle = title.toLowerCase();

    if (lowerTitle.includes('') || lowerTitle.includes('') || lowerTitle.includes('')) {
        return 'policy';
    }
    if (lowerTitle.includes('') || lowerTitle.includes('') || lowerTitle.includes('')) {
        return 'analysis';
    }
    if (lowerTitle.includes('') || lowerTitle.includes('') || lowerTitle.includes('gtx')) {
        return 'development';
    }
    return 'market';
}

/**
 *   
 */
function analyzeSentiment(title) {
    const positiveKeywords = ['', '', '', '', '', '', '', ''];
    const negativeKeywords = ['', '', '', '', '', '', '', ''];

    const lowerTitle = title.toLowerCase();

    for (const keyword of positiveKeywords) {
        if (lowerTitle.includes(keyword)) return 'positive';
    }
    for (const keyword of negativeKeywords) {
        if (lowerTitle.includes(keyword)) return 'negative';
    }
    return 'neutral';
}

/**
 *    (CORS-free API )
 */
async function fetchNewsFromBackup(category) {
    // AllOrigins  
    const query = encodeURIComponent(' ' + (category === 'all' ? '' : category));
    const googleNewsUrl = `https://news.google.com/rss/search?q=${query}&hl=ko&gl=KR&ceid=KR:ko`;

    try {
        const proxyUrl = `https://api.allorigins.win/raw?url=${encodeURIComponent(googleNewsUrl)}`;
        const response = await fetch(proxyUrl);
        const xmlText = await response.text();
        return parseRSSFeed(xmlText);
    } catch (error) {
        console.error('   :', error);
        return [];
    }
}

/**
 *    (Web Scraping )
 */
export const fetchNaverRealEstateNews = async () => {
    //   API   
    //  Google News RSS 
    return fetchRealEstateNews('all');
};
