import { memo, useCallback } from 'react';
import {
    LayoutDashboard,
    TrendingUp,
    Globe,
    MapPin,
    Calculator,
    Calendar,
    Receipt,
    Search,
    Bell,
    FileDown,
    BarChart2,
    Newspaper,
    ChevronLeft,
    ChevronRight,
    Sparkles,
    Settings,
    Train,
    LineChart,
    Activity,
    Target,
    Gauge,
    FlaskConical,
} from 'lucide-react';

/**
 *    - Premium Edition
 */
const Sidebar = memo(({ currentPage, onPageChange, isCollapsed, onToggle }) => {
    const menuGroups = [
        {
            title: '',
            items: [
                { id: 'dashboard', label: '', icon: LayoutDashboard },
                { id: 'market', label: ' ', icon: TrendingUp },
                { id: 'macro', label: '', icon: Globe },
                { id: 'regional', label: ' ', icon: MapPin },
            ]
        },
        {
            title: ' ',
            items: [
                { id: 'correlation', label: ' ', icon: Activity },
                { id: 'scenario', label: ' ', icon: FlaskConical },
                { id: 'cycle', label: '/', icon: Gauge },
                { id: 'leading', label: '', icon: Target },
            ]
        },
        {
            title: '',
            items: [
                { id: 'investment', label: ' ', icon: Calculator },
                { id: 'simulator', label: ' ', icon: LineChart },
                { id: 'tax', label: ' ', icon: Receipt },
                { id: 'search', label: ' ', icon: Search },
                { id: 'compare', label: ' ', icon: BarChart2 },
                { id: 'transport', label: ' ', icon: Train },
            ]
        },
        {
            title: '',
            items: [
                { id: 'calendar', label: ' ', icon: Calendar },
                { id: 'news', label: ' ', icon: Newspaper },
                { id: 'watchlist', label: ' ', icon: Bell },
                { id: 'insights', label: 'AI ', icon: Sparkles },
            ]
        },
    ];

    const handleNavClick = useCallback((id) => {
        onPageChange(id);
    }, [onPageChange]);

    return (
        <aside
            className="glass"
            style={{
                position: 'fixed',
                top: 0,
                left: 0,
                width: isCollapsed ? 72 : 280,
                height: '100vh',
                display: 'flex',
                flexDirection: 'column',
                transition: 'width var(--transition-normal)',
                zIndex: 30,
                overflow: 'hidden',
                borderRight: '1px solid var(--color-border)',
            }}
        >
            {/* Logo */}
            <div style={{
                padding: isCollapsed ? '20px 16px' : '20px 24px',
                borderBottom: '1px solid var(--color-border)',
                display: 'flex',
                alignItems: 'center',
                gap: 14,
                minHeight: 'var(--header-height)',
            }}>
                <div style={{
                    width: 42,
                    height: 42,
                    borderRadius: 'var(--radius-lg)',
                    background: 'var(--gradient-primary)',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: 'white',
                    flexShrink: 0,
                    boxShadow: '0 4px 12px rgba(99, 102, 241, 0.35)',
                    transition: 'transform var(--transition-normal)',
                }}>
                    <TrendingUp size={22} />
                </div>
                {!isCollapsed && (
                    <div style={{ overflow: 'hidden' }}>
                        <h1 style={{
                            fontSize: '1.1rem',
                            fontWeight: 700,
                            whiteSpace: 'nowrap',
                            background: 'var(--gradient-primary)',
                            WebkitBackgroundClip: 'text',
                            WebkitTextFillColor: 'transparent',
                            backgroundClip: 'text',
                        }}>
                             
                        </h1>
                        <p style={{
                            fontSize: '0.7rem',
                            color: 'var(--color-text-tertiary)',
                            whiteSpace: 'nowrap',
                            letterSpacing: '0.02em',
                        }}>
                            Real Estate Analytics Pro
                        </p>
                    </div>
                )}
            </div>

            {/* Navigation */}
            <nav style={{ flex: 1, padding: '16px 10px', overflowY: 'auto' }}>
                {menuGroups.map((group, groupIndex) => (
                    <div key={group.title} style={{ marginBottom: groupIndex < menuGroups.length - 1 ? 20 : 0 }}>
                        {!isCollapsed && (
                            <div style={{
                                padding: '8px 14px',
                                fontSize: '0.7rem',
                                fontWeight: 600,
                                color: 'var(--color-text-tertiary)',
                                textTransform: 'uppercase',
                                letterSpacing: '0.08em',
                            }}>
                                {group.title}
                            </div>
                        )}

                        {group.items.map(item => {
                            const isActive = currentPage === item.id;
                            const Icon = item.icon;

                            return (
                                <button
                                    key={item.id}
                                    onClick={() => handleNavClick(item.id)}
                                    title={isCollapsed ? item.label : undefined}
                                    style={{
                                        width: '100%',
                                        display: 'flex',
                                        alignItems: 'center',
                                        gap: 12,
                                        padding: isCollapsed ? '12px' : '11px 14px',
                                        marginBottom: 3,
                                        border: 'none',
                                        borderRadius: 'var(--radius-md)',
                                        background: isActive
                                            ? 'var(--gradient-primary)'
                                            : 'transparent',
                                        color: isActive ? 'white' : 'var(--color-text-secondary)',
                                        cursor: 'pointer',
                                        transition: 'all var(--transition-fast)',
                                        justifyContent: isCollapsed ? 'center' : 'flex-start',
                                        boxShadow: isActive ? '0 4px 12px rgba(99, 102, 241, 0.3)' : 'none',
                                        position: 'relative',
                                        overflow: 'hidden',
                                    }}
                                    onMouseEnter={(e) => {
                                        if (!isActive) {
                                            e.currentTarget.style.background = 'var(--color-bg-tertiary)';
                                            e.currentTarget.style.color = 'var(--color-text-primary)';
                                            e.currentTarget.style.transform = 'translateX(4px)';
                                        }
                                    }}
                                    onMouseLeave={(e) => {
                                        if (!isActive) {
                                            e.currentTarget.style.background = 'transparent';
                                            e.currentTarget.style.color = 'var(--color-text-secondary)';
                                            e.currentTarget.style.transform = 'translateX(0)';
                                        }
                                    }}
                                >
                                    <Icon size={18} style={{ flexShrink: 0 }} />
                                    {!isCollapsed && (
                                        <div style={{
                                            textAlign: 'left',
                                            overflow: 'hidden',
                                            flex: 1,
                                            fontSize: '0.85rem',
                                            fontWeight: isActive ? 600 : 500,
                                            whiteSpace: 'nowrap',
                                        }}>
                                            {item.label}
                                        </div>
                                    )}
                                    {isActive && !isCollapsed && (
                                        <div style={{
                                            width: 6,
                                            height: 6,
                                            borderRadius: '50%',
                                            background: 'white',
                                            opacity: 0.8,
                                        }} />
                                    )}
                                </button>
                            );
                        })}
                    </div>
                ))}
            </nav>

            {/* Bottom Actions */}
            {!isCollapsed && (
                <div style={{ padding: '14px 16px', borderTop: '1px solid var(--color-border)' }}>
                    <button
                        onClick={() => handleNavClick('export')}
                        className="btn btn-secondary"
                        style={{
                            width: '100%',
                            justifyContent: 'center',
                            fontSize: '0.8rem',
                            marginBottom: 8,
                        }}
                    >
                        <FileDown size={16} />
                         
                    </button>
                    <button
                        onClick={() => handleNavClick('settings')}
                        className="btn btn-ghost"
                        style={{ width: '100%', justifyContent: 'center', fontSize: '0.8rem' }}
                    >
                        <Settings size={16} />
                        
                    </button>
                </div>
            )}

            {/* Collapsed Settings Button */}
            {isCollapsed && (
                <div style={{ padding: '10px', borderTop: '1px solid var(--color-border)' }}>
                    <button
                        onClick={() => handleNavClick('settings')}
                        className="btn btn-ghost btn-icon"
                        style={{ width: '100%', justifyContent: 'center' }}
                        title=""
                    >
                        <Settings size={18} />
                    </button>
                </div>
            )}

            {/* Collapse Toggle */}
            <div style={{ padding: '14px', borderTop: '1px solid var(--color-border)' }}>
                <button
                    onClick={onToggle}
                    className="btn btn-ghost"
                    style={{
                        width: '100%',
                        justifyContent: 'center',
                        transition: 'all var(--transition-normal)',
                    }}
                >
                    <div style={{
                        transition: 'transform var(--transition-normal)',
                        transform: isCollapsed ? 'rotate(0deg)' : 'rotate(0deg)',
                    }}>
                        {isCollapsed ? <ChevronRight size={18} /> : <ChevronLeft size={18} />}
                    </div>
                </button>
            </div>
        </aside>
    );
});

Sidebar.displayName = 'Sidebar';

export default Sidebar;
