import { useMemo, useState } from 'react';
import {
    LineChart, Line, AreaChart, Area, BarChart, Bar, ComposedChart,
    XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend, ReferenceLine
} from 'recharts';
import { Percent, DollarSign, TrendingUp, BarChart3, Activity } from 'lucide-react';
import StatCard from '../components/ui/StatCard';
import ChartContainer from '../components/ui/ChartContainer';
import Tabs from '../components/ui/Tabs';
import {
    generateHistoricalData,
    generateInterestRateData,
    generateGDPData,
    generateM2Data
} from '../data';
import { formatNumber, formatPercent } from '../utils/formatters';

/**
 *   
 */
const MacroEconomics = () => {
    const [activeTab, setActiveTab] = useState('rate');

    const historicalData = useMemo(() => generateHistoricalData(), []);
    const rateData = useMemo(() => generateInterestRateData(), []);
    const gdpData = useMemo(() => generateGDPData(), []);
    const m2Data = useMemo(() => generateM2Data(), []);

    const latestRate = rateData[rateData.length - 1];
    const prevRate = rateData[rateData.length - 5];
    const latestGDP = gdpData[gdpData.length - 1];
    const latestM2 = m2Data[m2Data.length - 1];
    const latestCPI = historicalData[historicalData.length - 1];
    const prevCPI = historicalData[historicalData.length - 13];

    const cpiYoY = ((latestCPI.cpi - prevCPI.cpi) / prevCPI.cpi * 100);

    //     ()
    const correlationData = useMemo(() => {
        const yearlyHPI = {};
        historicalData.forEach(d => {
            if (!yearlyHPI[d.year]) yearlyHPI[d.year] = [];
            yearlyHPI[d.year].push(d.hpiSeoul);
        });

        return rateData
            .filter((_, i) => i % 4 === 3) //  
            .map(r => ({
                year: r.year,
                rate: r.rate,
                hpi: yearlyHPI[r.year]
                    ? yearlyHPI[r.year][yearlyHPI[r.year].length - 1]
                    : null,
            }))
            .filter(d => d.hpi !== null);
    }, [historicalData, rateData]);

    const tabs = [
        { id: 'rate', label: '', icon: Percent },
        { id: 'gdp', label: 'GDP', icon: BarChart3 },
        { id: 'm2', label: '(M2)', icon: DollarSign },
        { id: 'cpi', label: '(CPI)', icon: Activity },
    ];

    return (
        <div className="page-container">
            {/* Tabs */}
            <div style={{ marginBottom: 24 }}>
                <Tabs tabs={tabs} activeTab={activeTab} onChange={setActiveTab} />
            </div>

            {/* Stats Grid */}
            <div className="grid-stats" style={{ marginBottom: 24 }}>
                <StatCard
                    title=""
                    value={latestRate.rate}
                    suffix="%"
                    change={latestRate.rate - prevRate.rate}
                    changeLabel=" "
                    icon={Percent}
                    iconColor="#8b5cf6"
                    iconBg="#ede9fe"
                />
                <StatCard
                    title="GDP "
                    value={latestGDP.rate}
                    suffix="%"
                    subtitle={`${latestGDP.year}`}
                    icon={BarChart3}
                    iconColor="#10b981"
                    iconBg="#d1fae5"
                />
                <StatCard
                    title="M2 "
                    value={formatNumber(latestM2.amount)}
                    suffix=""
                    change={latestM2.growthRate}
                    changeLabel=" "
                    icon={DollarSign}
                    iconColor="#f59e0b"
                    iconBg="#fef3c7"
                />
                <StatCard
                    title=" "
                    value={cpiYoY.toFixed(1)}
                    suffix="%"
                    subtitle="  "
                    icon={Activity}
                    iconColor="#ef4444"
                    iconBg="#fee2e2"
                />
            </div>

            {/* Charts */}
            {activeTab === 'rate' && (
                <div className="grid-charts">
                    <ChartContainer
                        title=" "
                        subtitle=" "
                        height={400}
                    >
                        <ResponsiveContainer width="100%" height="100%">
                            <AreaChart data={rateData}>
                                <defs>
                                    <linearGradient id="gradientRate" x1="0" y1="0" x2="0" y2="1">
                                        <stop offset="5%" stopColor="#8b5cf6" stopOpacity={0.3} />
                                        <stop offset="95%" stopColor="#8b5cf6" stopOpacity={0} />
                                    </linearGradient>
                                </defs>
                                <CartesianGrid strokeDasharray="3 3" stroke="var(--color-border)" vertical={false} />
                                <XAxis
                                    dataKey="date"
                                    tick={{ fontSize: 11, fill: 'var(--color-text-tertiary)' }}
                                    minTickGap={40}
                                />
                                <YAxis
                                    tick={{ fontSize: 11, fill: 'var(--color-text-tertiary)' }}
                                    domain={[0, 6]}
                                    tickFormatter={(v) => `${v}%`}
                                />
                                <Tooltip
                                    contentStyle={{
                                        background: 'var(--color-bg-secondary)',
                                        border: '1px solid var(--color-border)',
                                        borderRadius: 'var(--radius-md)',
                                    }}
                                    formatter={(v) => [`${v}%`, '']}
                                />
                                <ReferenceLine y={2} stroke="var(--color-text-tertiary)" strokeDasharray="5 5" />
                                <Area
                                    type="stepAfter"
                                    dataKey="rate"
                                    stroke="#8b5cf6"
                                    fill="url(#gradientRate)"
                                    strokeWidth={2}
                                />
                            </AreaChart>
                        </ResponsiveContainer>
                    </ChartContainer>

                    <ChartContainer
                        title=" vs "
                        subtitle="  ()"
                        height={400}
                    >
                        <ResponsiveContainer width="100%" height="100%">
                            <ComposedChart data={correlationData}>
                                <CartesianGrid strokeDasharray="3 3" stroke="var(--color-border)" vertical={false} />
                                <XAxis
                                    dataKey="year"
                                    tick={{ fontSize: 11, fill: 'var(--color-text-tertiary)' }}
                                />
                                <YAxis
                                    yAxisId="left"
                                    tick={{ fontSize: 11, fill: 'var(--color-text-tertiary)' }}
                                    domain={[0, 6]}
                                    tickFormatter={(v) => `${v}%`}
                                />
                                <YAxis
                                    yAxisId="right"
                                    orientation="right"
                                    tick={{ fontSize: 11, fill: 'var(--color-text-tertiary)' }}
                                    domain={['auto', 'auto']}
                                />
                                <Tooltip
                                    contentStyle={{
                                        background: 'var(--color-bg-secondary)',
                                        border: '1px solid var(--color-border)',
                                        borderRadius: 'var(--radius-md)',
                                    }}
                                />
                                <Legend />
                                <Bar
                                    yAxisId="left"
                                    dataKey="rate"
                                    name="(%)"
                                    fill="#8b5cf6"
                                    opacity={0.6}
                                    radius={[4, 4, 0, 0]}
                                />
                                <Line
                                    yAxisId="right"
                                    type="monotone"
                                    dataKey="hpi"
                                    name=" HPI"
                                    stroke="#ef4444"
                                    strokeWidth={2.5}
                                    dot={{ fill: '#ef4444', r: 3 }}
                                />
                            </ComposedChart>
                        </ResponsiveContainer>
                    </ChartContainer>
                </div>
            )}

            {activeTab === 'gdp' && (
                <ChartContainer
                    title=" GDP "
                    subtitle=" GDP "
                    height={400}
                >
                    <ResponsiveContainer width="100%" height="100%">
                        <BarChart data={gdpData}>
                            <CartesianGrid strokeDasharray="3 3" stroke="var(--color-border)" vertical={false} />
                            <XAxis
                                dataKey="year"
                                tick={{ fontSize: 11, fill: 'var(--color-text-tertiary)' }}
                            />
                            <YAxis
                                tick={{ fontSize: 11, fill: 'var(--color-text-tertiary)' }}
                                domain={[-2, 10]}
                                tickFormatter={(v) => `${v}%`}
                            />
                            <Tooltip
                                contentStyle={{
                                    background: 'var(--color-bg-secondary)',
                                    border: '1px solid var(--color-border)',
                                    borderRadius: 'var(--radius-md)',
                                }}
                                formatter={(v) => [`${v}%`, 'GDP ']}
                            />
                            <ReferenceLine y={0} stroke="var(--color-text-primary)" />
                            <Bar
                                dataKey="rate"
                                fill="var(--color-success)"
                                radius={[4, 4, 0, 0]}
                            >
                                {gdpData.map((entry, index) => (
                                    <rect
                                        key={index}
                                        fill={entry.rate < 0 ? 'var(--color-danger)' : 'var(--color-success)'}
                                    />
                                ))}
                            </Bar>
                        </BarChart>
                    </ResponsiveContainer>
                </ChartContainer>
            )}

            {activeTab === 'm2' && (
                <ChartContainer
                    title="M2  "
                    subtitle=""
                    height={400}
                >
                    <ResponsiveContainer width="100%" height="100%">
                        <ComposedChart data={m2Data}>
                            <defs>
                                <linearGradient id="gradientM2" x1="0" y1="0" x2="0" y2="1">
                                    <stop offset="5%" stopColor="#f59e0b" stopOpacity={0.3} />
                                    <stop offset="95%" stopColor="#f59e0b" stopOpacity={0} />
                                </linearGradient>
                            </defs>
                            <CartesianGrid strokeDasharray="3 3" stroke="var(--color-border)" vertical={false} />
                            <XAxis
                                dataKey="year"
                                tick={{ fontSize: 11, fill: 'var(--color-text-tertiary)' }}
                            />
                            <YAxis
                                yAxisId="left"
                                tick={{ fontSize: 11, fill: 'var(--color-text-tertiary)' }}
                                tickFormatter={(v) => `${(v / 1000).toFixed(0)}`}
                            />
                            <YAxis
                                yAxisId="right"
                                orientation="right"
                                tick={{ fontSize: 11, fill: 'var(--color-text-tertiary)' }}
                                tickFormatter={(v) => `${v}%`}
                            />
                            <Tooltip
                                contentStyle={{
                                    background: 'var(--color-bg-secondary)',
                                    border: '1px solid var(--color-border)',
                                    borderRadius: 'var(--radius-md)',
                                }}
                            />
                            <Legend />
                            <Area
                                yAxisId="left"
                                type="monotone"
                                dataKey="amount"
                                name="M2 ()"
                                stroke="#f59e0b"
                                fill="url(#gradientM2)"
                                strokeWidth={2}
                            />
                            <Line
                                yAxisId="right"
                                type="monotone"
                                dataKey="growthRate"
                                name="(%)"
                                stroke="#3b82f6"
                                strokeWidth={2}
                                dot={false}
                            />
                        </ComposedChart>
                    </ResponsiveContainer>
                </ChartContainer>
            )}

            {activeTab === 'cpi' && (
                <ChartContainer
                    title=" vs "
                    subtitle="2000 = 100 "
                    height={400}
                >
                    <ResponsiveContainer width="100%" height="100%">
                        <LineChart data={historicalData.filter((_, i) => i % 6 === 0)}>
                            <CartesianGrid strokeDasharray="3 3" stroke="var(--color-border)" vertical={false} />
                            <XAxis
                                dataKey="date"
                                tick={{ fontSize: 11, fill: 'var(--color-text-tertiary)' }}
                                minTickGap={50}
                            />
                            <YAxis
                                tick={{ fontSize: 11, fill: 'var(--color-text-tertiary)' }}
                                domain={['auto', 'auto']}
                            />
                            <Tooltip
                                contentStyle={{
                                    background: 'var(--color-bg-secondary)',
                                    border: '1px solid var(--color-border)',
                                    borderRadius: 'var(--radius-md)',
                                }}
                            />
                            <Legend />
                            <Line type="monotone" dataKey="cpi" name="(CPI)" stroke="#94a3b8" strokeWidth={2} dot={false} />
                            <Line type="monotone" dataKey="hpiSeoul" name=" " stroke="#ef4444" strokeWidth={2.5} dot={false} />
                            <Line type="monotone" dataKey="hpiNation" name=" " stroke="#3b82f6" strokeWidth={2} dot={false} />
                        </LineChart>
                    </ResponsiveContainer>
                </ChartContainer>
            )}

            {/* Insight */}
            <div className="card" style={{ marginTop: 24 }}>
                <h3 style={{ fontSize: '1rem', fontWeight: 600, marginBottom: 12, display: 'flex', alignItems: 'center', gap: 8 }}>
                       
                </h3>
                <div style={{
                    display: 'grid',
                    gridTemplateColumns: 'repeat(auto-fit, minmax(280px, 1fr))',
                    gap: 16,
                    fontSize: '0.875rem',
                    color: 'var(--color-text-secondary)',
                    lineHeight: 1.7,
                }}>
                    <div style={{ padding: 16, background: 'var(--color-bg-tertiary)', borderRadius: 'var(--radius-md)' }}>
                        <strong style={{ color: 'var(--color-text-primary)' }}>     </strong>
                        <p style={{ marginTop: 8 }}>
                                    .
                            2022       .
                        </p>
                    </div>
                    <div style={{ padding: 16, background: 'var(--color-bg-tertiary)', borderRadius: 'var(--radius-md)' }}>
                        <strong style={{ color: 'var(--color-text-primary)' }}>     </strong>
                        <p style={{ marginTop: 8 }}>
                            M2         .
                            2020-2021      .
                        </p>
                    </div>
                    <div style={{ padding: 16, background: 'var(--color-bg-tertiary)', borderRadius: 'var(--radius-md)' }}>
                        <strong style={{ color: 'var(--color-text-primary)' }}>  </strong>
                        <p style={{ marginTop: 8 }}>
                                .  
                                .
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default MacroEconomics;
