/**
 * AI   - OpenAI API 
 */

// OpenAI API 
const OPENAI_API_URL = 'https://api.openai.com/v1/chat/completions';

/**
 * AI   
 */
export const generateMarketAnalysis = async (apiKey, marketData, topic = 'overall') => {
    if (!apiKey) {
        throw new Error('OpenAI API  .');
    }

    const prompts = {
        overall: `     .       .

 :
-  : ${marketData.seoulHPI || 'N/A'}
-  : ${marketData.nationHPI || 'N/A'}
- : ${marketData.interestRate || 'N/A'}%
-   : ${marketData.monthlyChange || 'N/A'}%

  :
1.    (2-3)
2.   4 (  )
3.   3
4.   3
5.   (2-3)`,

        seoul: `    . , ,       .`,

        interest: `  ${marketData.interestRate}%     .      .`,

        forecast: `2026     .  ,  ,   .`,
    };

    const prompt = prompts[topic] || prompts.overall;

    try {
        const response = await fetch(OPENAI_API_URL, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${apiKey}`,
            },
            body: JSON.stringify({
                model: 'gpt-4o-mini',
                messages: [
                    {
                        role: 'system',
                        content: '     .     ,      .',
                    },
                    {
                        role: 'user',
                        content: prompt,
                    },
                ],
                temperature: 0.7,
                max_tokens: 1000,
            }),
        });

        if (!response.ok) {
            const error = await response.json();
            throw new Error(error.error?.message || 'API  ');
        }

        const data = await response.json();
        const content = data.choices[0]?.message?.content || '';

        return parseAIResponse(content, topic);
    } catch (error) {
        console.error('AI   :', error);
        throw error;
    }
};

/**
 * AI  
 */
function parseAIResponse(content, topic) {
    const lines = content.split('\n').filter(line => line.trim());

    //  
    const result = {
        title: getTopicTitle(topic),
        summary: '',
        keyPoints: [],
        risks: [],
        opportunities: [],
        forecast: '',
        raw: content,
        generatedAt: new Date().toISOString(),
    };

    let currentSection = 'summary';

    lines.forEach(line => {
        const trimmed = line.trim();

        //  
        if (trimmed.includes('') || trimmed.includes('')) {
            currentSection = 'summary';
        } else if (trimmed.includes('') || trimmed.includes('')) {
            currentSection = 'keyPoints';
        } else if (trimmed.includes('') || trimmed.includes('')) {
            currentSection = 'risks';
        } else if (trimmed.includes('')) {
            currentSection = 'opportunities';
        } else if (trimmed.includes('') || trimmed.includes('')) {
            currentSection = 'forecast';
        } else {
            //  
            const cleanLine = trimmed.replace(/^[\d\-\*\\.]+\s*/, '').trim();

            if (cleanLine) {
                switch (currentSection) {
                    case 'summary':
                        result.summary += (result.summary ? ' ' : '') + cleanLine;
                        break;
                    case 'keyPoints':
                        if (result.keyPoints.length < 5) result.keyPoints.push(cleanLine);
                        break;
                    case 'risks':
                        if (result.risks.length < 4) result.risks.push(cleanLine);
                        break;
                    case 'opportunities':
                        if (result.opportunities.length < 4) result.opportunities.push(cleanLine);
                        break;
                    case 'forecast':
                        result.forecast += (result.forecast ? ' ' : '') + cleanLine;
                        break;
                }
            }
        }
    });

    //   
    if (result.keyPoints.length === 0) {
        result.keyPoints = ['    .'];
    }

    return result;
}

/**
 *  
 */
function getTopicTitle(topic) {
    const currentDate = new Date();
    const year = currentDate.getFullYear();
    const month = currentDate.getMonth() + 1;

    const titles = {
        overall: `${year} ${month}    `,
        seoul: '    ',
        interest: '     ',
        forecast: `${year}    `,
    };
    return titles[topic] || titles.overall;
}

/**
 *     (API )
 */
export const generateSimpleAnalysis = async (marketData, recentNews = []) => {
    //   
    const sentimentCounts = { positive: 0, negative: 0, neutral: 0 };
    recentNews.forEach(news => {
        sentimentCounts[news.sentiment || 'neutral']++;
    });

    const dominantSentiment = Object.entries(sentimentCounts)
        .sort((a, b) => b[1] - a[1])[0][0];

    //    
    const monthlyChange = marketData.monthlyChange || 0;
    const interestRate = marketData.interestRate || 3.5;

    let marketTrend = 'neutral';
    if (monthlyChange > 0.5) marketTrend = 'positive';
    else if (monthlyChange < -0.5) marketTrend = 'negative';

    return {
        title: `${new Date().toLocaleDateString('ko-KR')}  `,
        summary: generateSummary(marketTrend, dominantSentiment, interestRate),
        sentiment: dominantSentiment,
        keyPoints: generateKeyPoints(marketData, dominantSentiment),
        risks: generateRisks(interestRate, monthlyChange),
        opportunities: generateOpportunities(interestRate, monthlyChange),
        generatedAt: new Date().toISOString(),
        isAI: false,
    };
};

function generateSummary(trend, sentiment, rate) {
    const trendText = {
        positive: '  ',
        negative: '  ',
        neutral: '  ',
    };

    const sentimentText = {
        positive: '  .',
        negative: '   .',
        neutral: '  .',
    };

    return `   ${trendText[trend]}.  ${rate}%  ${sentimentText[sentiment]}`;
}

function generateKeyPoints(data, sentiment) {
    const points = [];

    if (data.seoulHPI) {
        points.push(`  ${data.seoulHPI.toFixed(1)} `);
    }
    if (data.monthlyChange) {
        const direction = data.monthlyChange > 0 ? '' : '';
        points.push(`  ${Math.abs(data.monthlyChange).toFixed(2)}% ${direction}`);
    }
    if (data.interestRate) {
        points.push(` ${data.interestRate}%  `);
    }

    points.push(sentiment === 'positive' ? '   ' : ' ');

    return points;
}

function generateRisks(rate, change) {
    const risks = [];

    if (rate >= 3.5) risks.push('      ');
    if (change < 0) risks.push('     ');
    risks.push('   ');
    risks.push('  ');

    return risks.slice(0, 3);
}

function generateOpportunities(rate, change) {
    const opportunities = [];

    if (rate < 4) opportunities.push('   ');
    if (change < 0) opportunities.push('   ');
    opportunities.push('    ');
    opportunities.push('  ');

    return opportunities.slice(0, 3);
}

/**
 * API   
 */
export const validateOpenAIKey = async (apiKey) => {
    try {
        const response = await fetch('https://api.openai.com/v1/models', {
            headers: {
                'Authorization': `Bearer ${apiKey}`,
            },
        });

        return response.ok;
    } catch (error) {
        return false;
    }
};
