import { memo, useMemo } from 'react';
import { TrendingUp, TrendingDown, Minus } from 'lucide-react';
import { formatNumber, formatPercent, getTrendClass } from '../../utils/formatters';

/**
 *    ( )
 */
const StatCard = memo(({
    title,
    value,
    subtitle,
    change,
    changeLabel,
    icon: Icon,
    iconColor = 'var(--color-primary)',
    iconBg = 'var(--color-primary-light)',
    prefix = '',
    suffix = '',
    loading = false,
}) => {
    //   
    const trendIcon = useMemo(() => {
        if (change > 0) return <TrendingUp size={14} />;
        if (change < 0) return <TrendingDown size={14} />;
        return <Minus size={14} />;
    }, [change]);

    //   
    const formattedValue = useMemo(() => {
        return typeof value === 'number' ? formatNumber(value, 1) : value;
    }, [value]);

    if (loading) {
        return (
            <div className="stat-card">
                <div className="stat-card-header">
                    <div className="skeleton" style={{ width: 100, height: 16 }} />
                    <div className="skeleton" style={{ width: 40, height: 40, borderRadius: 'var(--radius-md)' }} />
                </div>
                <div className="skeleton" style={{ width: 120, height: 32 }} />
                <div className="skeleton" style={{ width: 80, height: 14 }} />
            </div>
        );
    }

    return (
        <div className="stat-card animate-slide-up">
            <div className="stat-card-header">
                <span className="stat-card-label">{title}</span>
                {Icon && (
                    <div
                        className="stat-card-icon"
                        style={{
                            backgroundColor: iconBg,
                            color: iconColor,
                        }}
                    >
                        <Icon size={20} />
                    </div>
                )}
            </div>

            <div className="stat-card-value number-display">
                {prefix}{formattedValue}{suffix}
            </div>

            <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
                {change !== undefined && (
                    <span className={`stat-card-change ${getTrendClass(change)}`}>
                        {trendIcon}
                        {formatPercent(Math.abs(change))}
                    </span>
                )}
                {(subtitle || changeLabel) && (
                    <span className="stat-card-label">
                        {changeLabel || subtitle}
                    </span>
                )}
            </div>
        </div>
    );
});

StatCard.displayName = 'StatCard';

export default StatCard;

