export const generateSupplyDemandData = () => {
    const data = [];

    const baseSupply = {
        2015: { newConstruction: 380, permits: 420, unsold: 45 },
        2016: { newConstruction: 400, permits: 450, unsold: 40 },
        2017: { newConstruction: 420, permits: 480, unsold: 35 },
        2018: { newConstruction: 450, permits: 520, unsold: 30 },
        2019: { newConstruction: 380, permits: 400, unsold: 40 },
        2020: { newConstruction: 350, permits: 380, unsold: 35 },
        2021: { newConstruction: 320, permits: 350, unsold: 25 },
        2022: { newConstruction: 380, permits: 320, unsold: 55 },
        2023: { newConstruction: 420, permits: 280, unsold: 70 },
        2024: { newConstruction: 380, permits: 300, unsold: 65 },
        2025: { newConstruction: 350, permits: 320, unsold: 60 },
    };

    for (let year = 2015; year <= 2025; year++) {
        const base = baseSupply[year] || baseSupply[2015];
        data.push({
            year,
            newConstruction: base.newConstruction + Math.round((Math.random() - 0.5) * 20),
            permits: base.permits + Math.round((Math.random() - 0.5) * 30),
            unsold: base.unsold + Math.round((Math.random() - 0.5) * 5),
            //  
            newConstructionLabel: `${base.newConstruction}`,
            permitsLabel: `${base.permits}`,
            unsoldLabel: `${base.unsold}`,
        });
    }

    return data;
};

//   ( - )
export const generateRealInterestRateData = () => {
    const data = [];

    const nominalRates = {
        2015: 1.50, 2016: 1.25, 2017: 1.25, 2018: 1.75,
        2019: 1.25, 2020: 0.50, 2021: 1.00, 2022: 3.25,
        2023: 3.50, 2024: 3.25, 2025: 3.00,
    };

    const inflationRates = {
        2015: 0.7, 2016: 1.0, 2017: 1.9, 2018: 1.5,
        2019: 0.4, 2020: 0.5, 2021: 2.5, 2022: 5.1,
        2023: 3.6, 2024: 2.8, 2025: 2.2,
    };

    for (let year = 2015; year <= 2025; year++) {
        const nominal = nominalRates[year];
        const inflation = inflationRates[year];

        data.push({
            year,
            nominal,
            inflation,
            real: parseFloat((nominal - inflation).toFixed(2)),
        });
    }

    return data;
};

//   (M2/GDP, /GDP)
export const generateLiquidityIndicators = () => {
    const data = [];

    for (let year = 2010; year <= 2025; year++) {
        // M2/GDP  (  )
        let m2GdpRatio = 120 + (year - 2010) * 5;
        if (year >= 2020) m2GdpRatio += 15; //   
        if (year >= 2022) m2GdpRatio -= 5; // 

        // /GDP 
        let householdDebtRatio = 75 + (year - 2010) * 3;
        if (year >= 2021) householdDebtRatio += 5;

        data.push({
            year,
            m2GdpRatio: parseFloat((m2GdpRatio + (Math.random() - 0.5) * 3).toFixed(1)),
            householdDebtRatio: parseFloat((householdDebtRatio + (Math.random() - 0.5) * 2).toFixed(1)),
        });
    }

    return data;
};

//   
export const generateGlobalComparisonData = () => {
    const data = [];

    const baseData = {
        korea: { rate2020: 0.5, rate2025: 3.0, hpi2020: 100, hpi2025: 105 },
        usa: { rate2020: 0.25, rate2025: 4.5, hpi2020: 100, hpi2025: 115 },
        japan: { rate2020: -0.1, rate2025: 0.25, hpi2020: 100, hpi2025: 110 },
        china: { rate2020: 3.85, rate2025: 3.1, hpi2020: 100, hpi2025: 95 },
    };

    for (let year = 2020; year <= 2025; year++) {
        const progress = (year - 2020) / 5;

        data.push({
            year,
            korea: {
                rate: parseFloat((baseData.korea.rate2020 + (baseData.korea.rate2025 - baseData.korea.rate2020) * progress).toFixed(2)),
                hpi: parseFloat((baseData.korea.hpi2020 + (baseData.korea.hpi2025 - baseData.korea.hpi2020) * progress).toFixed(1)),
            },
            usa: {
                rate: parseFloat((baseData.usa.rate2020 + (baseData.usa.rate2025 - baseData.usa.rate2020) * progress).toFixed(2)),
                hpi: parseFloat((baseData.usa.hpi2020 + (baseData.usa.hpi2025 - baseData.usa.hpi2020) * progress).toFixed(1)),
            },
            japan: {
                rate: parseFloat((baseData.japan.rate2020 + (baseData.japan.rate2025 - baseData.japan.rate2020) * progress).toFixed(2)),
                hpi: parseFloat((baseData.japan.hpi2020 + (baseData.japan.hpi2025 - baseData.japan.hpi2020) * progress).toFixed(1)),
            },
            china: {
                rate: parseFloat((baseData.china.rate2020 + (baseData.china.rate2025 - baseData.china.rate2020) * progress).toFixed(2)),
                hpi: parseFloat((baseData.china.hpi2020 + (baseData.china.hpi2025 - baseData.china.hpi2020) * progress).toFixed(1)),
            },
        });
    }

    return data;
};

