import { useState, useCallback } from 'react';
import { DataProvider } from './context/DataContext';
import Sidebar from './components/Sidebar';
import Header from './components/Header';
import Dashboard from './pages/Dashboard';
import MarketAnalysis from './pages/MarketAnalysis';
import MacroEconomics from './pages/MacroEconomics';
import RegionalAnalysis from './pages/RegionalAnalysis';
import InvestmentTools from './pages/InvestmentTools';
import InvestmentSimulator from './pages/InvestmentSimulator';
import TransportAnalysis from './pages/TransportAnalysis';
import EconomicCalendar from './pages/EconomicCalendar';
import TaxCalculator from './pages/TaxCalculator';
import PropertySearch from './pages/PropertySearch';
import CompareAnalysis from './pages/CompareAnalysis';
import NewsFeed from './pages/NewsFeed';
import Watchlist from './pages/Watchlist';
import AIInsights from './pages/AIInsights';
import DataExport from './pages/DataExport';
import SettingsPage from './pages/SettingsPage';
import CorrelationAnalyzer from './pages/CorrelationAnalyzer';
import ScenarioSimulator from './pages/ScenarioSimulator';
import CycleAnalysis from './pages/CycleAnalysis';
import LeadingIndicators from './pages/LeadingIndicators';
import { useTheme } from './hooks/useTheme';

/**
 *      (Pro )
 */
function AppContent() {
    const [currentPage, setCurrentPage] = useState('dashboard');
    const [sidebarCollapsed, setSidebarCollapsed] = useState(false);
    const [mobileSidebarOpen, setMobileSidebarOpen] = useState(false);
    const { theme, toggleTheme } = useTheme();

    const handlePageChange = useCallback((page) => {
        setCurrentPage(page);
        setMobileSidebarOpen(false);
    }, []);

    const toggleSidebar = useCallback(() => {
        setSidebarCollapsed(prev => !prev);
    }, []);

    const toggleMobileSidebar = useCallback(() => {
        setMobileSidebarOpen(prev => !prev);
    }, []);

    const pageTitle = {
        dashboard: '',
        market: ' ',
        macro: ' ',
        regional: ' ',
        correlation: ' ',
        scenario: ' ',
        cycle: '/ ',
        leading: ' ',
        investment: ' ',
        simulator: ' ',
        transport: ' ',
        tax: ' ',
        search: ' ',
        compare: ' ',
        calendar: ' ',
        news: ' ',
        watchlist: ' ',
        insights: 'AI ',
        export: ' ',
        settings: '',
    };

    const renderPage = () => {
        switch (currentPage) {
            case 'dashboard':
                return <Dashboard />;
            case 'market':
                return <MarketAnalysis />;
            case 'macro':
                return <MacroEconomics />;
            case 'regional':
                return <RegionalAnalysis />;
            case 'correlation':
                return <CorrelationAnalyzer />;
            case 'scenario':
                return <ScenarioSimulator />;
            case 'cycle':
                return <CycleAnalysis />;
            case 'leading':
                return <LeadingIndicators />;
            case 'investment':
                return <InvestmentTools />;
            case 'simulator':
                return <InvestmentSimulator />;
            case 'transport':
                return <TransportAnalysis />;
            case 'tax':
                return <TaxCalculator />;
            case 'search':
                return <PropertySearch />;
            case 'compare':
                return <CompareAnalysis />;
            case 'calendar':
                return <EconomicCalendar />;
            case 'news':
                return <NewsFeed />;
            case 'watchlist':
                return <Watchlist />;
            case 'insights':
                return <AIInsights />;
            case 'export':
                return <DataExport />;
            case 'settings':
                return <SettingsPage />;
            default:
                return <Dashboard />;
        }
    };

    return (
        <div className="app-layout">
            {/* Sidebar */}
            <Sidebar
                currentPage={currentPage}
                onPageChange={handlePageChange}
                isCollapsed={sidebarCollapsed}
                onToggle={toggleSidebar}
            />

            {/* Mobile Sidebar Overlay */}
            {mobileSidebarOpen && (
                <div
                    style={{
                        position: 'fixed',
                        inset: 0,
                        backgroundColor: 'rgba(0, 0, 0, 0.5)',
                        zIndex: 25,
                    }}
                    onClick={() => setMobileSidebarOpen(false)}
                />
            )}

            {/* Main Content */}
            <div
                className="main-content"
                style={{
                    marginLeft: sidebarCollapsed ? 72 : 280,
                    transition: 'margin-left var(--transition-normal)',
                }}
            >
                <Header
                    title={pageTitle[currentPage]}
                    theme={theme}
                    onToggleTheme={toggleTheme}
                    sidebarCollapsed={sidebarCollapsed}
                    onToggleMobileSidebar={toggleMobileSidebar}
                    isMobileSidebarOpen={mobileSidebarOpen}
                />

                <main style={{ paddingTop: 'var(--header-height)' }}>
                    {renderPage()}
                </main>
            </div>

            {/* Mobile Responsive Styles */}
            <style>{`
        @media (max-width: 1024px) {
          .main-content {
            margin-left: 0 !important;
          }
          
          aside {
            transform: translateX(-100%);
            transition: transform var(--transition-normal);
          }
          
          ${mobileSidebarOpen ? `
            aside {
              transform: translateX(0) !important;
            }
          ` : ''}
        }
      `}</style>
        </div>
    );
}

// DataProvider  
function App() {
    return (
        <DataProvider>
            <AppContent />
        </DataProvider>
    );
}

export default App;
